"use client";

import { useMemo } from "react";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Button } from "@/components/ui/button";
import { useAppTheme } from "@/components/app-theme-context";

const COLOR_OPTIONS = [
  "zinc",
  "slate",
  "gray",
  "stone",
  "red",
  "rose",
  "orange",
  "amber",
  "yellow",
  "lime",
  "green",
  "emerald",
  "teal",
  "cyan",
  "sky",
  "blue",
  "indigo",
  "violet",
  "purple",
  "fuchsia",
  "pink",
] as const;

type ColorName = (typeof COLOR_OPTIONS)[number];

export default function ThemeColorPicker() {
  const { palette, setPalette } = useAppTheme();
  const items = useMemo(() => COLOR_OPTIONS, []);

  return (
    <div className="flex items-center gap-3">
      <Select
        value={palette || undefined}
        onValueChange={(val: ColorName) => {
          setPalette(val);
        }}
      >
        <SelectTrigger className="w-52">
          <SelectValue placeholder="Pick theme color" />
        </SelectTrigger>
        <SelectContent>
          {items.map((c) => (
            <SelectItem key={c} value={c}>
              {c}
            </SelectItem>
          ))}
        </SelectContent>
      </Select>
      <Button
        variant="secondary"
        onClick={() => {
          setPalette("");
        }}
      >
        Reset
      </Button>
    </div>
  );
}